import Vue from 'vue';
import Vuex from 'vuex';
Vue.use(Vuex);
export default new Vuex.Store({
    state: {
        // server: 'http://localhost:5000/',
        server: window.location.href,
        recentLinks: new Array(),
        newTuple: null,
        connectionError: false
    },
    mutations: {
        updateRecentLinks: function (state, arr) {
            var _a;
            (_a = state.recentLinks).push.apply(_a, arr);
        },
        addToRecentLinkStack: function (state, tuple) {
            var lastItem = JSON.parse(JSON.stringify(state.recentLinks))[0];
            if (lastItem.slug != tuple.slug) {
                state.recentLinks.unshift(tuple);
            }
        },
        updateNewLink: function (state, tuple) {
            state.newTuple = tuple;
        },
        updateConnectionError: function (state, bool) {
            state.connectionError = bool;
        }
    },
    actions: {},
    modules: {}
});
